<template>
  <div>
    <div class="cms-list-layout">
      <!-- 不同平台切换 -->
      <el-tabs v-model="queryParams.platformType" @tab-click="changeTabs">
        <el-tab-pane label="平台管理" name="1"></el-tab-pane>
        <el-tab-pane label="商家管理" name="2"></el-tab-pane>
      </el-tabs>
      <cms-prompt>
        <li>顶部批量操作按钮均为需要选中数据才能进行操作；</li>
      </cms-prompt>
      <section class="cms-list-query">
        <div class="query-group">
          <label>api地址</label>
          <el-input v-model="queryParams.apiUrl" @change="query"></el-input>
        </div>
        <div class="query-group">
          <label>api名称</label>
          <el-input v-model="queryParams.apiName" @change="query"></el-input>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
      <cms-sticky>
      <section class="cms-batch">
        <cms-button icon="success" type="success" size="small" v-prem="'/apiMange/save'"
         @click="$routerLink('/apiMange/save','save',{platformType:queryParams.platformType})">新增API
        </cms-button>
        <cms-button icon="error" type="error" size="small"  v-prem="'/apiMange/delete'"
         @click="deleteDatas()">批量删除
        </cms-button>
      </section>
      </cms-sticky>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="apiName" label="api名称" min-width="180">
          <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.apiName"> {{scope.row.apiName}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="apiUrl" label="api地址" min-width="300">
         <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.apiUrl"> {{scope.row.apiUrl}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="requestMethod" label="请求方式" width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.requestMethod==1">GET</span>
            <span v-if="scope.row.requestMethod==2">POST</span>
            <span v-if="scope.row.requestMethod==3">DELETE</span>
            <span v-if="scope.row.requestMethod==4">UPDATE</span>
          </template>
        </el-table-column>
        <el-table-column prop="useScene" label="使用场景" min-width="200">
          <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.useScene">
              {{scope.row.useScene}}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width='175' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button icon='edit' size='small' type='info'  v-prem="'/apiMange/update'"
             @click.stop="$routerLink('/apiMange/update','update',{id:scope.row.id,platformType:queryParams.platformType})">编辑
            </cms-button>
            <cms-button icon='error' size='small' type='info'  v-prem="'/apiMange/delete'"
             @click.stop="deleteData(scope.row.id)">删除
            </cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  apiManagerList,
  apiManagerDelete
} from './../../../../api/admin/apiManger.js'
var defaultData = {
  apiName: '',
  apiUrl: '',
  size: 10,
  page: 1,
  platformType: '1'
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      typeList: []
    }
  },
  computed: {},
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(apiManagerDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(apiManagerDelete, this.ids) // 这个才是真实的删除方法
    },
    getTypeList() {
      // 获取字典数据
      // this.typeList = this.$getDictData('systemType')
      // this.queryParams.platformType = this.typeList[0].dictCode // 默认取第一个
      this.getTableData(apiManagerList, this.queryParams)
    },
    changeTabs(val, event) {
      // 不跳页的选项卡切换
      this.queryParams.platFormCod = val.name
      this.queryParams.page = 1
      this.getTableData(apiManagerList, this.queryParams)
    },
    codeName(id) {
      // 字典没有这里会报错
      const item = this.typeList.filter(v => {
        if (v.dictCode === id) {
          return v
        }
      })
      return item[0].dictLabel
    }
  },
  created() {
    this.getTypeList()
  }
}
</script>

<style>
</style>



